@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.blackspot-parent-1,.blackspot-parent-2,.blackspot-parent-3.blackspot-parent-4{
  width: 150px;
  height: 150px;
  -webkit-transform: 0.7s;
          transform: 0.7s;

}

.blackspot-parent-1{
  position: absolute;
  margin-left: 20vw;
  margin-top: 50vh;
  transition: 0.7s;
  z-index:3;
  
}
.blackspot-parent-1 :hover{
  margin-left: 10px;
  transition: 0.7s;
}
.blackspot-parent-2{
  position: absolute;
  margin-left: 70vw;
  margin-top: 75vh;
  z-index:5;
}
.blackspot-parent-2 :hover{
  margin-left: -10px;
  margin-top: -10px;
  transition: 0.7s;
}
.blackspot-parent-3{
  position: absolute;
  margin-left: 10vw;
  margin-top: 15vh;
  z-index:3;
}
.blackspot-parent-3 :hover{
  margin-left: 10px;
  margin-top: 10px;
  transition: 0.7s;
}
.blackspot-parent-4{
  position: absolute;
  margin-left: 80vw;
  margin-top: 10vh;
  z-index:3;
  
}
.blackspot-parent-4 :hover{
  margin-left: -10px;
  margin-top: -10px;
  transition: 0.7s;
}
.blackspot-1 , .blackspot-2, .blackspot-3, .blackspot-4{
  border-radius: 50%;
  width: 150px;
  height: 150px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  border:1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
 
}

.blackspot-1{
  -webkit-animation: roatation_1 60s;
          animation: roatation_1 60s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.blackspot-2{
  -webkit-animation: roatation_2 90s;
          animation: roatation_2 90s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.blackspot-3{
  -webkit-animation: roatation_3 40s;
          animation: roatation_3 40s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.blackspot-4{
  -webkit-animation: roatation_4 80s;
          animation: roatation_4 80s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes roatation_1 {
  0%{
    rotate: 0deg;
  }
  100%{
    rotate: 360deg;

  }
}

@keyframes roatation_1 {
  0%{
